
import XHeader from 'vux/src/components/x-header/index.vue'

import { getTkinfo } from 'api'
export default {
data () {
return {
info: null,
cc: this.$route.params.cc
}
},
created () {
this.getTkinfo()
},
methods: {
getTkinfo () {
this.$vux.loading.show()
getTkinfo({cc: this.cc}).then(res => {
this.info = res.data
})
.finally(() => {
this.$vux.loading.hide()
})
}
},
components: {
XHeader
}
}



